.portfolioTitle-- {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.portfolioTitle {
    display: grid;
    grid-template-rows: auto 1fr;
    margin: 5px;
}

.portfolioTitle- {
    border-radius: 10px;
    background-color: white;
    display: grid;
    justify-items: center;
}

.cardImageContainer {
    display: flex;
    justify-content: center;
    padding-bottom: 15px;
}

.cardImage {
    height: 100px;
    width: 100px;
}

.grid-container {
    display: grid;
    grid-template-rows: 10% 80% 1fr 5%;
    padding: 60px;
}

.griddiv {
    background-color: white;
    border-style: solid;
    border-color: black;
    width: 250px;
    height: 350px;
    padding: 40px;
    border-radius: 15px;
    border-width: 5px;
    justify-content: center;
    transition: all 0.05s linear;
    cursor: pointer;
}

.images {
    display: grid;
    grid-gap: 100px;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-template-rows: 1fr;
    justify-items: center;
    align-items: center;
}