.name {
    padding-left: 7px; 
}

.rs-navbar {
    background-color: #f0db4f;
    height: 108px;
    padding-top: 8px;
    padding-bottom: 8px;
}

#logo {
    display: flex;
    padding-bottom: 72px;
}

#logoName {
    font-family: codec-header;
    font-size: 3em;
    color:#323330;
}

#logoImg {
    width: 100px;
    height: 100px;
}

#logoTitle {
    font-family: codec-subtitle;
}

@media (max-width: 762px) {

    .rs-navbar {
        background-color: #f0db4f;
        height: 75px;
        padding-top: 2px;
        padding-bottom: 2px;
    }

    #logoName {
        font-family: codec-header;
        font-size: 2.5em;
        color:#323330;
    }

    #logoImg {
        width: 70px;
        height: 70px;
    }

    #pic {
        margin-top: 12%;
    }

}

@media (max-width: 591px) {
    .rs-navbar-nav.rs-navbar-right {
        float: left;
    }

    .rs-navbar {
        background-color: #f0db4f;
        height: 130px;
        padding-top: 2px;
        padding-bottom: 2px;
    }

    #logoName {
        font-family: codec-header;
        font-size: 2em;
        color:#323330;
    }

    #logoImg {
        width: 56px;
        height: 56px;
    }
}

@media (max-width: 680px) {
    .rs-navbar-nav.rs-navbar-right {
        float: left;
    }

    .rs-navbar {
        background-color: #f0db4f;
        height: 130px;
        padding-top: 2px;
        padding-bottom: 2px;
    }

    #logoName {
        font-family: codec-header;
        font-size: 2.5em;
        color:#323330;
    }

    #pic {
        margin-top: 18%
    }

}
  